import React, { useEffect, useMemo } from 'react';
import './MysticForest.css';

const Firefly = ({ id }) => {
  // Memoize random values so they don't change on re-render
  const styles = useMemo(() => ({
    top: `${Math.random() * 100}%`,
    left: `${Math.random() * 100}%`,
    animationName: `float${Math.ceil(Math.random() * 3)}`,
    animationDuration: `${Math.random() * 10 + 5}s`,
    animationDelay: `${Math.random() * 10}s`,
  }), []);

  return React.createElement('div', { 
    className: 'firefly', 
    style: styles 
  });
};

const MysticForest = () => {
  useEffect(() => {
    const handleMouseMove = (e) => {
      const container = document.getElementById('forest-container');
      if (container) {
        const { clientX, clientY, currentTarget } = e;
        const { clientWidth, clientHeight } = currentTarget;
        
        const x = (clientX / clientWidth - 0.5) * 2;
        const y = (clientY / clientHeight - 0.5) * 2;
        
        container.style.setProperty('--mouse-x', x);
        container.style.setProperty('--mouse-y', y);
      }
    };
    
    const container = document.getElementById('forest-container');
    container.addEventListener('mousemove', handleMouseMove);

    return () => {
      container.removeEventListener('mousemove', handleMouseMove);
    };
  }, []);

  const fireflies1 = useMemo(() => Array.from({ length: 10 }, (_, i) => React.createElement(Firefly, { key: i, id: i })), []);
  const fireflies2 = useMemo(() => Array.from({ length: 10 }, (_, i) => React.createElement(Firefly, { key: i, id: i })), []);

  return React.createElement(
    'div',
    { 
      id: 'forest-container', 
      className: 'mystic-forest' 
    },
    [
      React.createElement(
        'div',
        { className: 'forest-layer layer-bg', key: 'layer-bg' }
      ),
      React.createElement(
        'div',
        { className: 'forest-layer layer-fog', style: { zIndex: 2 }, key: 'layer-fog' }
      ),
      React.createElement(
        'div',
        { 
          className: 'fireflies-container', 
          style: { zIndex: 4 }, 
          key: 'fireflies-container-1' 
        },
        fireflies1
      ),
      React.createElement(
        'div',
        { className: 'forest-layer layer-3', key: 'layer-3' }
      ),
      React.createElement(
        'div',
        { 
          className: 'fireflies-container', 
          style: { zIndex: 6 }, 
          key: 'fireflies-container-2' 
        },
        fireflies2
      ),
      React.createElement(
        'div',
        { className: 'forest-layer layer-2', key: 'layer-2' }
      ),
      React.createElement(
        'div',
        { className: 'forest-layer layer-1', key: 'layer-1' }
      )
    ]
  );
};

export default MysticForest;